define('aeonvera/controllers/events/show/revenue', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Controller.extend({
    startTime: null,
    endTime: null,
    startDate: null,
    endDate: null,

    startDateTime: (function () {
      var startDate = this.get('startDate');
      var startTime = this.get('startTime');
      var parsed = null;

      if (Ember['default'].isPresent(startDate)) {
        parsed = moment(startDate);
      }

      if (Ember['default'].isPresent(startTime)) {
        var timeArray = startTime.split(':');
        var hours = timeArray[0];
        var minutes = timeArray[1];
        parsed.hours(hours);
        parsed.minutes(minutes);
      }

      return parsed;
    }).property('startTime', 'startDate'),

    endDateTime: (function () {
      var endDate = this.get('endDate');
      var endTime = this.get('endTime');
      var parsed = null;

      if (Ember['default'].isPresent(endDate)) {
        parsed = moment(endDate);
      }

      if (Ember['default'].isPresent(endTime)) {
        var timeArray = endTime.split(':');
        var hours = timeArray[0];
        var minutes = timeArray[1];
        parsed.hours(hours);
        parsed.minutes(minutes);
      }

      return parsed;
    }).property('endTime', 'endDate'),

    filteredOrders: (function () {
      var model = this.get('model');
      var startTime = this.get('startDateTime');
      var endTime = this.get('endDateTime');

      var filtered = model;

      if (startTime != null) {
        filtered = filtered.filter(function (item) {
          var time = item.get('createdAt');
          var isAfterStartTime = moment(time).isAfter(startTime);
          return isAfterStartTime;
        });
      }

      if (endTime != null) {
        filtered = filtered.filter(function (item) {
          var time = item.get('createdAt');
          var isBeforeEndTime = moment(time).isBefore(endTime);
          return isBeforeEndTime;
        });
      }

      return filtered;
    }).property('model', 'startDateTime', 'endDateTime'),

    actions: {
      updateTime: function updateTime(time) {
        console.log(time);
      }
    }
  });

});