define('aeonvera/helpers/to-usd', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Helper.helper(function (params) {
    var value = params[0];

    if (value === undefined) {
      return value;
    }

    var amount = (value || 0).toFixed(2),
        sign = "$";

    return "" + sign + amount;
  });

});